}

.navbar {
  padding: 20px;
  background-color: rgba(107, 235, 192, 0.719);
  box-shadow: 4px 0 20px -5px rgba(0, 0, 0, 0.2);
}

.navbar .logo-navbar img {
  width: 45px;
  margin-right: 5px;
}

.navbar .logo-navbar a {
  width: 15%;
  font-weight: bold;
  color: #fff !important;
}

.navbar .logo-navbar a:hover {
  color: #fff;
}

.navbar ul {
  margin-left: auto;
}

.navbar ul li a {
  font-family: Poppins-Medium;
  font-size: 18px;
  font-weight: 500;
  color: #fff !important;
}

.navbar ul .dropdown ul li a {
  color: black !important;
}

.navbar-toggler {
  border-color: rgba(0, 0, 0, 0) !important;
}

.Navbar .form-control, .form-button {
