}

.about {
  height: 100px;
}

.about h2 {
  color: #31c3f0;
  font-family: inherit;
  text-align: center;
  font-weight: bold;
}

.latest {
  margin-right: 15px;
  width: 15rem;
  border-radius: 10px;
  box-shadow: 2px 2px 30px rgba(0, 0, 0, 0.2);
}

.latest img {
  border-radius: 10px;
}

.latest p {
  font-weight: bold;
  text-align: center;
  padding-bottom: 10px;
}

.App {
  background-color: #131720;
}

.card {
  border: none;
  background-color: #131720;
  max-height: 400px;
}

.card .card__cover {
  display: -webkit-flex;
