/* Footer starts */
footer {
  background-color: #131924;
}

footer .btn-floating {
  border-radius: 30px;
  background-color: #d1cbcb;
  width: 8%;
}

footer .btn-floating img {
  width: 100%;
}

footer h2 {
  color: #afa6a6;
  font-size: 3rem;
  text-align: center;
}

