}

/* Footer ends */
.plus {
  position: absolute;
  top: 5px;
  right: 5px;
  z-index: 1;
  width: 15%;
}

.plus .dropdown-menu {
  border-radius: 10px;
  background: #045377;
}

.plus .dropdown-menu .dropdown-item {
  font-size: 14px;
  color: #fff;
  font-family: 'Inconsolata', monospace;
}

.plus .dropdown-menu .dropdown-item:hover {
  background-color: #045377;
}

.plus .dropdown-menu .dropdown-item::before {
  background: #f5c518;
  content: '';
  margin-left: -.45rem;
  margin-top: 2px;
  -webkit-align-self: flex-start;
          align-self: flex-start;
  position: absolute;
  height: 13%;
  width: 3px;
}

.plus .dropdown-menu .trailer, .plus .dropdown-menu .last-item {
  text-align: center;
  color: rgba(107, 235, 192, 0.719) !important;
}

.plus img {
  width: 100%;
  -webkit-filter: none !important;
          filter: none !important;
}

.Blog h3 {
