.filter-button-keeper h5 {
  font-size: 20px;
  padding: 2px 0px;
  color: #beb7b7;
  border: 1px solid black;
  cursor: pointer;
}

.filter-button-keeper h5:hover {
  background-color: #fff !important;
  transition: ease-in-out 0.4s;
}
