@media only screen and (max-width: 576px) {
    footer .btn-floating{
        width: 12%;
    }
    .plus .dropdown-item{
        font-size: 12.5px!important;
    }
    .top-info{
        font-size: 20px;
    }
    .filter-button-keeper h5{
        font-size: 16px;
    }
    .welcome3{
        display: none;
    }
}
@media only screen and (max-width: 451px) {
    .filter-button-keeper h5{
        font-size: 12px;
    }
}
@media only screen and (max-width: 1200px) {
    .Blog .top-info{
        font-size: 1.17rem;
    }
}

